body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
 background-color: #fff;
}
input {
  color: #000;
  outline: 0;
  border: 0;
  opacity: 0.4;
  font-size: 40px;
  font-weight: 900;
  text-align: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 35px;
  left: 0;
  margin: auto;
  width: 600px;
  height: 50px;
  border-radius: 5px;
 }
button {
  color: #c91;
  outline: 0;
  border: 0;
  opacity: 0.4;
  font-size: 40px;
  font-weight: 900;
  text-align: center;
  position: absolute;
  top: 250px;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 55px;
  height: 45px;
  border-radius: 10px;

}

.App {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: minmax(100px, auto);
}
.search-box {
  grid-column: 1
}
.video-view {
  grid-column: 2;
  grid-row: 1;
}
.videos {
  grid-column:2;
}

